.signup {
  display: flex;
  flex-direction: column;
  width: 38rem;
  padding: 4rem 3rem 3rem;

  background: #ffffff;
  border: 1px solid rgba(0, 0, 0, 0.12);
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.2);
  border-radius: 4px;

  &__header {
    font-family: Open Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 24px;
    line-height: 30px;
    /* identical to box height */

    /* Dark 100% */

    color: #1f2041;
  }

  & > * {
    margin-bottom: 1rem;
  }
}
